import Vue from 'vue'
import VueRouter from 'vue-router'
import home from "@/views/home/Home";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home,
    children: [

      {
        path: '/',
        name: 'article',
        component: () => import(/* webpackChunkName: "about" */ '../views/article/article.vue')

      }
    ]
  }


]

const router = new VueRouter({
  routes
})

export default router
